  display: inline;
  list-style-type: none; }
  .u-inline-list li a {
    text-decoration: none; }

.u-underline-none {
  text-decoration: none; }

header:focus {
  outline: none; }

section {
  margin-top: 2rem;
  margin-bottom: 2rem; }
  section:focus {
    outline: none; }

h1.page-title {
  margin-bottom: 0rem; }

h2.page-subtitle {
  font-size: 3rem;
  margin: 0rem; }

img.page-subtitle-image {
  position: relative;
  max-width: 50px;
  margin-left: 1rem;
  margin-right: 1rem;
  top: 0.5rem; }

#navigation-toggle {
  font-size: 4rem;
  margin-top: 2rem; }

.top-navigation {
  display: none; }

.top-navigation li {
  float: none;
  display: block;
  padding-bottom: 0rem;
  margin-top: 0.5rem;
  text-decoration: none;
  font-size: 2.4rem; }
  .top-navigation li a {
    text-decoration: none; }

.experience-item h3.experience-item-company {
  margin-bottom: 0rem; }

.experience-item .experience-item-logo {
  max-width: 165px;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  margin-left: 0.5rem; }

.experience-item h4.experience-item-position {
  margin-bottom: 0rem; }

.skill-list i {
  font-size: 3.3rem; }
  .skill-list i:hover {
    color: #1e384f; }

.skill-list-checkmarks {
  list-style-type: none;
  margin-left: 2.5rem;
  padding-left: 0; }
  .skill-list-checkmarks li {
    position: relative; }
    .skill-list-checkmarks li i {
      left: -2em;
      position: absolute;
      text-align: center;
      width: 2em;
      line-height: inherit; }

footer .footer-tagline {
  font-size: 3rem;
  font-weight: 600; }
  footer .footer-tagline i {
    font-weight: 600; }

footer img.page-subtitle-image {
  margin: 0rem; }

.music-header-container {
  width: 100%;
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .music-header-container img.music-header-logo {
    max-width: 100px;
    position: relative; }

#navigation-toggle.music-toggle {
  position: initial; }

/* Larger than phablet (also point when grid becomes active) */
@media (min-width: 550px) {
  #navigation-toggle.music-toggle {
    position: absolute;
    right: 0; }
  .music-header-container {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    align-items: center; }
    .music-header-container img.music-header-logo {
      max-width: 100px;
      position: relative;
      left: -30px; } }
