// Color Variables
$black: black;
$hover: #1e384f;
//Importing Normalize
@import "normalize";
//Importing Skeleton
@import "skeleton";
//Importing Devicons
@import "devicon";
//Importing FontAwesome
@import "fontawesome";
//Importing Fontello
@import "fontello";
// Additional Utility Classes
.u-text-bold {
  font-weight: 600;
}
.u-text-center {
  text-align: center;
}
.u-text-left {
  text-align: left;
}
.u-text-right {
  text-align: right;
}
.u-inline-list {
  li {
    display: inline;
    list-style-type: none;
    a {
      text-decoration: none;
    }
  }
}
.u-underline-none {
  text-decoration: none;
}
// Header Styles
header {
  &:focus {
    outline: none;
  }
}
// Section Styles 
section {
  margin-top: 2rem;
  margin-bottom: 2rem;
  &:focus {
    outline: none;
  }
}
// Page Title Styles
h1.page-title {
  margin-bottom: 0rem;
}
h2.page-subtitle {
  font-size: 3rem;
  margin: 0rem;
}
img.page-subtitle-image {
  position: relative;
  max-width: 50px;
  margin-left: 1rem;
  margin-right: 1rem;
  top: 0.5rem;
}
// Navigation Styles 
#navigation-toggle {
  font-size: 4rem;
  margin-top: 2rem;
}
.top-navigation {
  display: none;
}
.top-navigation li {
  float: none;
  display: block;
  padding-bottom: 0rem;
  margin-top: 0.5rem;
  text-decoration: none;
  font-size: 2.4rem;
  a {
    text-decoration: none;
  }
}
// Experience Item Styles
.experience-item {
  h3.experience-item-company {
    margin-bottom: 0rem;
  }
  .experience-item-logo {
    max-width: 165px;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    margin-left: 0.5rem;
  }
  h4.experience-item-position {
    margin-bottom: 0rem;
  }
}
// Skill List
.skill-list {
  i {
    font-size: 3.3rem;
    &:hover {
      color: $hover;
    }
  }
}
.skill-list-checkmarks {
  list-style-type: none;
  margin-left: 2.5rem;
  padding-left: 0;
  li {
    position: relative;
    i {
      left: -2em;
      position: absolute;
      text-align: center;
      width: 2em;
      line-height: inherit;
    }
  }
}
// Footer
footer {
  .footer-tagline {
    font-size: 3rem;
    font-weight: 600;
    i {
      font-weight: 600;
    }
  }
  img.page-subtitle-image {
    margin: 0rem;
  }
}
// Music Specific Page Styles
.music-header-container {
  width: 100%;
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  img.music-header-logo {
    max-width: 100px;
    position: relative;
    // left: -30px;
  }
}

#navigation-toggle.music-toggle {
  position: initial
}

/* Larger than phablet (also point when grid becomes active) */
@media (min-width: 550px) {
  #navigation-toggle.music-toggle {
    position: absolute;
    right: 0;
  }

  .music-header-container {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    img.music-header-logo {
      max-width: 100px;
      position: relative;
      left: -30px;
    }
  }
}

